<template>
<div>
  腾讯会议API测试1
  <div>
    X-TC-Key(SecretID):{{x_TC_Key}}<br>
    X-TC-Timestamp:{{x_TC_Timestamp}}<br>
    X-TC-Nonce:{{x_TC_Nonce}}<br>
    AppId:{{appId}}<br>
    SdkId:{{sdkId}}<br>
    sign：{{msg}}<br>
  </div>
  <div>
    <el-button type="primary" @click="getSign">获取签名</el-button>
  </div>
</div>
</template>

<script>
import request from "@/utils/request";

export default {
  name: "TXHYDemo1",


  data(){
    return{
      x_TC_Key:"g8dW5m4RkOMDyZnx3AevYCS1so3FxAjN",
      secretKey:"KOjfC18p7Q9uJNB7Tfgaz6AMdK19OKlYeGGHZJxXZCq77F9p",
      x_TC_Timestamp:"1626278171",
      x_TC_Nonce:"263040",
      appId:"210033730",
      sdkId:"16256516478",
     requestBody:{},
      msg:"腾讯会议签名"
    }
  },
  methods:{
    getSign(){
      let timeStamp=Date.parse(new Date()).toString()
      this.x_TC_Timestamp=timeStamp.substr(0,10)

      let nonce=(Math.random()*10000000).toString();
      this.x_TC_Nonce=nonce.substr(0,6)



      console.log(this)


      request.get("/txhy/sign",{
        params:{
          x_TC_Timestamp:this.x_TC_Timestamp,
          x_TC_Nonce:this.x_TC_Nonce,
          httpMethod:"get",
          URI:"/v1/users/list",
          requestBody:this.requestBody
        }
      }).then(res =>{
        console.log(res)
        this.msg=res.data.x_TC_Signature
      }).catch(function(err) {
        console.log(err)
      })

    }
  }
}
</script>

